 .App {
 display: flex;
  justify-content: center; 
  
}
.parent
{
  position: relative;
  width:100%;
  height: 100vh;
  z-index: 0;
}
.Bottom
{
  position:absolute;
  bottom:10px;
  right: 150px;
  z-index: 1;
} 
.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
} 
